import {
  Component,
  OnInit,
  OnDestroy,
  Inject,
  HostListener
} from '@angular/core';
import {
  trigger,
  style,
  animate,
  transition,
  query,
  stagger,
  keyframes,
  animateChild
} from '@angular/animations';
import { DOCUMENT } from '@angular/common';

import { HelpdeskService } from './../../core/services/helpdesk.service';
import { ProfileService } from './../../core/services/profile.service';
import { RequestsService } from './../../core/services/requests.service';
import { LangService } from './../../core/services/references/lang.service';
import { WINDOW } from './../../core/services/window.service';

@Component({
  selector: 'itx-helpdesk-list',
  templateUrl: './helpdesk-list.component.html',
  styleUrls: ['./helpdesk-list.component.scss'],
  animations: [
    trigger('slide', [
      transition(':enter', [
        animate(
          '250ms ease-in-out',
          keyframes([
            style({ transform: 'translateY(80%)', opacity: 0, offset: 0 }),
            style({ transform: 'translateY(-10%)', opacity: 0.8, offset: 0.7 }),
            style({ transform: 'translateY(5%)', opacity: 1, offset: 0.9 }),
            style({ transform: 'translateY(0)', opacity: 1, offset: 1 })
          ])
        )
      ]),
      transition(':leave', [
        animate(
          '250ms ease-in-out',
          keyframes([
            style({ transform: 'translateY(0)', opacity: 1, offset: 0 }),
            style({ transform: 'translateY(80%)', opacity: 0, offset: 1 })
          ])
        )
      ])
    ]),
    trigger('showCard', [
      transition(':enter', [
        query(':enter', stagger(50, [animateChild()]), { optional: true })
      ]),
      transition(':leave', [
        query(':leave', stagger(50, [animateChild()]), { optional: true })
      ])
    ])
  ]
})
export class HelpdeskListComponent implements OnInit, OnDestroy {
  public translation;

  public openRequests: Array<any> = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}];
  public closedRequests: Array<any> = [{}, {}, {}, {}, {}, {}, {}, {}, {}, {}];
  public requestStatusActiveTab: number;

  public requestOnLoad: boolean;

  public displayOpenRequests = true;
  public displayClosedRequests = true;

  private numberToLoad = 10;
  private count = 1;
  private loadAt = 300;

  private openRequestsObserver;
  private closedRequestsObserver;

  constructor(
    private helpdeskService: HelpdeskService,
    private langService: LangService,
    private profileService: ProfileService,
    private requestsService: RequestsService,
    @Inject(DOCUMENT) private document: Document,
    @Inject(WINDOW) private window: Window
  ) {
    this.translation = this.langService.lang;

    this.requestStatusActiveTab = 0;
    this.requestOnLoad = true;

    this.openRequestsObserver = this.requestsService.openProfileHelpdeskRequests$.subscribe(
      requests => {
        this.openRequests.splice(this.openRequests.length - 10, 10);
        requests.forEach(request => {
          this.openRequests.push(request);
        });
        this.displayOpenRequests = this.openRequests.length > 0 ? true : false;
        this.requestOnLoad = false;
      }
    );

    this.closedRequestsObserver = this.requestsService.closedProfileHelpdeskRequests$.subscribe(
      requests => {
        this.closedRequests.splice(this.closedRequests.length - 10, 10);
        requests.forEach(request => {
          this.closedRequests.push(request);
        });
        this.displayClosedRequests = this.closedRequests.length ? true : false;
        this.requestOnLoad = false;
      }
    );
  }

  ngOnInit(): void {
    this.getOpenHelpdeskRequests();
    this.getClosedHelpdeskRequests();
  }

  ngOnDestroy(): void {
    this.openRequestsObserver.unsubscribe();
    this.closedRequestsObserver.unsubscribe();
  }

  private getOpenHelpdeskRequests(): void {
    this.requestsService.getOpenProfileHelpdeskRequests();
  }

  private getClosedHelpdeskRequests(): void {
    this.requestsService.getClosedProfileHelpdeskRequests();
  }

  public activateTab(tab: number): void {
    this.requestStatusActiveTab = tab;
    this.count = 1;
  }

  public trackByRequest(index: number, request: any): number {
    return index;
  }
}
